body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body {
  height: 100vh;
  width: 100vw;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-wrap: wrap;
}
#map {
	width: 100vw;
	height: 85vh;
}

@media screen and (max-width: 400px) {
	#map {
		height: 90vh;
	}
}
nav {
	display: flex;
	width: 100vw;
	height: 10vh;
}

nav h1 {
	text-align: center;
	width: 100%;
	letter-spacing: 0.3rem;
	font-weight: normal;
}

#hamburger {
	height: 100%;
	width: 10vh;
	font-size: 3em;
	text-align: center;
	background: #fff;
	color: #000;
	z-index: 3;
	cursor: pointer;
	outline: none;
}

#hamburger:hover {
	background: #000;
	color: #fff;
}



@media screen and (max-width: 400px) {
	nav {
		height: 10vh;
	}
	nav h1 {
		font-size: 1.2em;
	}
}
footer {
	width: 100vw;
	height: 5vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

@media screen and (max-width: 400px) {
	footer {
		display: none;
	}	
}
#listing {
	position: fixed;
	height: 100vh;
	top: 0;
	box-shadow: #000 5px 0 10px;
	z-index: 2;
	background: #fff;
	-webkit-transition: 0.5s ease left, 0.7s linear width;
	transition: 0.5s ease left, 0.7s linear width;
	overflow: auto;
}

#searchbar {
	display: block;
	margin: 12vh auto 0 auto;
	width: 100%;
	padding: 20px;
	font-size: 1.2em;
	box-sizing: border-box;
}

#searchbar:focus {
	border: 3px solid cyan;
}

#venueList {
	list-style: none;
	width: 100%;
	padding: 20px 0px;
}

#venueList li {
	width: 100%;
	padding: 10px 20px;
	box-sizing: border-box;
}

#venueList li:hover,
#venueList li:nth-of-type(even):hover {
	background: #aaa;
	cursor: pointer;
	color: white;
	text-decoration: underline;
}

#venueList li:nth-of-type(even) {
	background: #eee;
}

.show-lists {
	left: 0;
	width: 300px;
}

.hide-lists {
	left: -1000px;
	width: 0;
}

.distance {
	color: darkolivegreen;
}
